.Footer {
    position: fixed;
    width: 100%;
    height: 5vh;
    bottom: 0%;
    background-color:aliceblue;
    z-index: 1000;
}

#blue-dicor {
    width: 15%;
    position: absolute;
    right: 0%;
    rotate: 180deg;
    bottom: 0%;
}

@media screen and (max-width: 768px) {
    #blue-dicor {
        width: 25%;
        position: absolute;
        right: 0%;
        rotate: 180deg;
        bottom: 0%;
    }
}